.Profile {
  padding: 20px 4vw;
}

.Profile__introduction, .Profile__tech_stack, .Profile__career {
  padding-left: 10px;
  border-left: 4px solid var(--main-color-dark);
}

.Profile__tech_stack > div {
  display: flex;
  align-items: center;

  padding: 10px;
  border-radius: 10px;

  background: var(--sub-color);
}

.Profile__tech_stack-logo {
  height: 8vw;
  margin-right: 4vw;
}

.Profile__career-name {
  font-weight: bold;
}

.Profile__career-content {
  margin-bottom: 10px;
}